
@charset "utf-8";
/* 禁用iPhone中Safari的字号自动调整 */
html {
    -webkit-text-size-adjust: 100%;
    -ms-text-size-adjust: 100%;
}
/* 去除iPhone中默认的input样式 */
input[type="submit"],

input[type="reset"],

input[type="button"],

input{-webkit-appearance:none; resize: none;
}
/* 取消链接高亮  */
body,div,ul,li,ol,h1,h2,h3,h4,h5,h6,input,textarea,select,p,dl,dt,dd,a,img,button,form,table,th,tr,td,tbody,article,
aside, details,figcaption,figure,footer,header,hgroup, menu,nav,section{ -webkit-tap-highlight-color:rgba(0, 0, 0, 0);
}
/* 设置HTML5元素为块 */
article, aside, details,figcaption,figure,footer,header,hgroup, menu,nav,section {
display: block;
}
/* 图片自适应 */
img {
max-width: 100%;
height: auto;
width:auto\9; /* ie8 */
-ms-interpolation-mode:bicubic;/*为了照顾ie图片缩放失真*/
}
/* 初始化 */
body,div,ul,li,ol,h1,h2,h3,h4,h5,h6,input,textarea,select,p,dl,dt,dd,a,img,button,form,table,th,tr,td,tbody,article,
aside, details,figcaption,figure,footer,header,hgroup, menu,nav,section{margin:0; padding:0; border:none;
}
body{font-family: '\5FAE\8F6F\96C5\9ED1',Microsoft YaHei,Tahoma,Arial,sans-serif; color:#555; background-color:#fff;
}
em,i{font-style:normal;
}
strong{font-weight: normal;
}
.clearfix:after{content:""; display:block; visibility:hidden; height:0; clear:both;
}
.clearfix{zoom:1;
}
a{text-decoration:none; color:#969696; font-family: '\5FAE\8F6F\96C5\9ED1',Microsoft YaHei,Tahoma,Arial,sans-serif;
}
a:hover{color:#FED503; text-decoration:none;
}
ul,ol{list-style:none;
}
h1, h2, h3, h4, h5, h6{ font-size:100%; font-family: Microsoft YaHei;
}
img{border: none;
}
.wrap{min-width:320px; max-width:540px;margin:0 auto;
}
/* 400 以下   400  -  480 一档  480 以上的是一档  */
@media screen and (max-width:399px) {
html{
        font-size: 15px;
}
}
@media (min-width: 400px) and (max-width: 480px) {
html{
        font-size: 20px;
}
}
/*对于设计图来说，我们是用这个作为相对单位*/
@media screen and (min-width: 481px) {
html{
        font-size: 25px;
}
}
